<template>
	<Dashboard>
		<div>
			<div class="mb-4 text-white">
				<h5>File Terkait Dokumen</h5>
				<small>File Terkait Dokumen</small>
			</div>
			
			<div class="card mb-2">
				<div class="card-body" v-if="document != null">
					
					<router-link to="/permit"><i class="fas fa-arrow-left"></i> Kembali ke list</router-link>
					<hr>
					
					<table class="table table-bordered">
						<tbody>
							<tr>
								<td width="20%">Kode Dokumen</td>
								<td>{{ document.number }}</td>
							</tr>
							<tr>
								<td>Nama Dokumen</td>
								<td>{{ selectedData(docPermitNames, document.permitId) }}</td>
							</tr>
							<tr>
								<td>Deskripsi Dokumen</td>
								<td>{{ document.description }}</td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
			
			<div class="card mb-2">
				<div class="card-body">
					<div class="row">
						<div class="col-md-8">
							<h5>File</h5>
							<p>file terkait untuk dokumen</p>
						</div>
						<div class="col-md-4 text-right">
							<button @click="chooseFile" class="btn btn-success"><i class="fas fa-upload"></i> Upload File</button>
						</div>
					</div>
					<hr>
					<table class="table table-bordered">
						<thead>
							<tr>
								<th>No</th>
								<th>Nama File</th>
								<th>Download</th>
							</tr>
						</thead>
						<tbody>
							<tr v-if="files.length == 0">
								<td colspan="3" class="text-center">Belum Ada File</td>
							</tr>
							<tr v-else v-for="(v, i) in files" :key="i">
								<td width="5%">{{ i+1 }}</td>
								<td width="80%">{{ v.path }}</td>
								<td width="15%">
									<a :href="downloadFile(v.path)" class="btn btn-sm btn-success"><i class="fas fa-download"></i></a>
									<button @click="doDelete(v.id)" class="btn btn-sm btn-danger ml-1"><i class="fas fa-trash"></i></button>
								</td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
			
			<div class="modal fade" id="modalAdd" tabindex="-1" role="dialog" aria-labelledby="modalStatus" aria-hidden="true"></div>
			
			<input type="file" @change="event => uploadDoc(event)" id="file-input" style="display: none;">
			
		</div>
		
	</Dashboard>
</template>

<script type="text/javascript">
	
	import alertMixin from '@/mixins/alert'
	import documentMixin from '@/mixins/document'
	import utilMixin from '@/mixins/util'
	import { documentById } from '@/api/document'
	import { fileUpload, getUploaded, deleteFile } from '@/api/file'
	
	export default{
		name: 'Attachment',
		data(){
			return{
				document: null,
				files: [],
				docPermitNames: [],
			}
		},
		props: [
		'id'
		],
		mixins: [
		alertMixin,
		documentMixin,
		utilMixin,
		],
		async mounted(){
			this.docPermitNames = await this.getDocPermitName();
			this.getDocument();
		},
		methods: {
			async getDocument(){
				$("#file-input").val("");
				this.showLoading();
				this.document = null;
				this.schedules = [];
				try{
					let request = await documentById(this.id);
					this.document = request.data[0].document;
					await this.getUploadedFile();
				}catch(e){
					console.log(e);
					this.hideLoading();
					this.showAlert("Gagal Mendapatkan Data", "error");
					if(error.response?.status == 401){
						this.invalidSession();
					}
				}
			},
			async getUploadedFile(){
				this.files = [];
				try{
					let request = await getUploaded(this.id);
					this.hideLoading();
					let data = request.data;
					data.forEach((v,i) => {
						this.files.push(v.fileAttachment[0]);
					})
				}catch(e){
					console.log(e);
					this.hideLoading();
					this.showAlert("Gagal Mendapatkan Data", "error");
				}
			},
			chooseFile(){
				$("#file-input").click();
			},
			async uploadDoc(elem){
				let input = $(elem.target);
				let target = elem.target.files[0];
				let allowed = ['jpg', 'JPG', 'png', 'PNG', 'pdf', 'jpeg'];
				let ext = target.name.split('.').pop();
				let size = target.size / 1000;
				
				this.showLoading();
				
				try{
					let formData = new FormData();
					formData.append("file", target);
					
					let request = await fileUpload(this.id, formData);
					this.hideLoading();
					$("#file-input").val("");
					this.showAlert(request.data);
					this.getDocument();
				}catch(e){
					$("#file-input").val("");
					console.log(e);
					this.hideLoading();
					this.showAlert("Gagal Mengupload File", "error");
					if(error.response?.status == 401){
						this.invalidSession();
					}
				}
			},
			doDelete(id){
				this.$swal({
					title: 'Hapus Data',
					text: "Yakin ingin menghapus file ?",
					type: 'error',
					showCancelButton: true,
					confirmButtonColor: '#3085d6',
					cancelButtonColor: '#d33',
					confirmButtonText: 'Yes'
				}).then((result) => {
					if (result.value) {
						this.showLoading();
						let request = deleteFile(id);
						request
						.then((response) => {
							this.hideLoading();
							
							this.$toast.open({
								message: 'File Berhasil Dihapus !',
								type: 'info',
								position: 'top-right',
							});
							
							this.getDocument();
						})
						.catch((error) => {
							this.hideLoading();
							this.showNotif("Terjadi Kesalahan");
							console.log(error);
							if(error.response?.status == 401){
								this.invalidSession();
							}
						})
					}
				});
			},
			async invalidSession(){
				alert("Sesi sudah tidak valid, silahkan login");
				await this.$store.dispatch("auth/authLogout");
				this.$router.replace("/login");
			}
		}
	}
</script>