import { httpClient,  httpClientFile } from './httpClient';

const END_POINT = '/api/file';

const fileUpload = (documentId, data) => {
	return httpClientFile.post(END_POINT + "/upload/" + documentId, data);
}
const getUploaded = (documentId) => {
	return httpClient.get(END_POINT + "/all/document_id/" + documentId);
}
const deleteFile = (fileId) => {
	return httpClient.delete(END_POINT + "/id/" + fileId);
}

export {
	fileUpload,
	getUploaded,
	deleteFile,
}